import { useCallback, useState } from "react";
import { useSelector } from "react-redux";

/*this is a more leaner version of useFetch - good for doing api GET requests and handling the data received */

const useGet = () => {
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(true);
  const token = localStorage.getItem("token");

  const sendRequest = useCallback(
    async (url: string, applyData: any) => {
      setLoading(true);
      try {
        const response = await fetch(url, {
          headers: token ? { token } : {},
        });

        if (!response.ok) {
          setLoading(false);
          const err = await response.json();
          console.log(err.message);

          throw new Error(err.message);
        }

        if (response.ok) {
          setLoading(false);
          try {
            // check if the response is JSON format - if not do nothing
            const data = await response.json();
            applyData && applyData(data);
          } catch (e) {}
        }
      } catch (e: any) {
        setError(e);
        setLoading(false);
      }
    },
    [token]
  );
  return {
    error,
    loading,
    sendRequest,
  };
};

export default useGet;
