import jwtDecode from "jwt-decode";
import { clientEnum, TokenValues } from "../Redux-TS/Reducers/authSlice";

export function useGetDetails(): TokenValues {
  const token = localStorage.getItem("token");
  if (token) {
    return jwtDecode(token);
  }
  return {
    exp: 0,
    iat: 0,
    id: 0,
    firstName: "",
    lastName: "",
    clientType: clientEnum.USER,
    sub: "",
  };
}
