import {
  Alert,
  Container,
  Fab,
  Stack,
  Tooltip,
  Typography,
} from "@mui/material";
import { useEffect, useState } from "react";
import TripCard from "./TripCard";
import AddIcon from "@mui/icons-material/Add";
import EmptyListCard from "../Layout/EmptyListCard";
import useGet from "../../Hooks/useGet";
import appConfig from "../../Utils/Config";
import Skeleton from "@mui/material/Skeleton";
import { useDispatch, useSelector } from "react-redux";
import { setTripList } from "../../Redux-TS/Reducers/tripListSlice";
import useTripService from "../../Service-Hooks/useTripService";

const TripList = (props) => {
  const [trips, setTrips] = useState([]);
  const tripList = useSelector((state) => state.tripList.tripList);
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  // const { error, loading, sendRequest: getTripData } = useGet();
  const tripService = useTripService();

  useEffect(() => {
    setLoading(true);
    (async () => {
      tripService.getAllTrips().then(
        (arr) => {
          setTrips(arr);
          setLoading(false);
        }
        // (err) => setAlert(getErrorMessage(err), "error")
      );
    })();
  }, []);

  // useEffect(() => {
  //   if (loading && tripList.length === 0) {
  //     const updateTrips = (tripsObj) => {
  //       dispatch(setTripList(tripsObj));
  //     };
  //     getTripData(appConfig.baseUrl + "user", updateTrips);
  //   }

  //   setTrips(tripList);
  // }, [dispatch, getTripData, loading, tripList]);

  const sk = [1, 2, 3, 4, 5, 6];

  return (
    <>
      <Container sx={{ mt: 1 }}>
        {tripList.length === 0 && loading && (
          <Stack>
            {sk.map((i) => (
              <Skeleton
                variant="rectangular"
                width={360}
                height={120}
                sx={{ my: 1 }}
                animation="wave"
                key={i}
              />
            ))}
          </Stack>
        )}
        {/* {tripList.length === 0 && error && (
          <Alert severity="error" sx={{ p: 2 }}>
            <Typography color="error">
              <strong>Error: </strong>
              {error.message}
            </Typography>
          </Alert>
        )} */}
        {!loading && trips.length === 0 && (
          <EmptyListCard
            title="No Trips Found"
            text="add your first trip"
            addTrip={props.handleAddTrip}
          />
        )}
        {tripList.length > 0 &&
          tripList.map((trip) => <TripCard data={trip} key={trip.id} />)}
      </Container>
      <Tooltip title="add new trip">
        <Fab
          onClick={props.handleAddTrip}
          color="secondary"
          sx={{ position: "fixed", bottom: 16, right: 20 }}
        >
          <AddIcon />
        </Fab>
      </Tooltip>
    </>
  );
};

export default TripList;
