import { useCallback, useState } from "react";

const useLogin = () => {
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState(false);

  const sendRequest = useCallback(async (configObj, applyData) => {
    //run useLogin only if configObj is passed
    if (configObj) {
      setError("");
      setLoading(true);
      let url = configObj.url;
      if (configObj.params) {
        url += "?" + new URLSearchParams(configObj.params);
      }

      try {
        const response = await fetch(url, {
          method: configObj.method ? configObj.method : "GET",
          headers: configObj.headers ? configObj.headers : {},
          body: configObj.body ? configObj.body : null,
        });

        if (!response.ok) {
          setLoading(false);
          setResponse(false);
          const err = await response.json();
          throw new Error(err.message);
        }

        if (response.ok) {
          setResponse(true);
          setLoading(false);

          try {
            // check if the response is text format - if not do nothing
            const data = await response.text();
            console.log(data);
            applyData && applyData(data);
          } catch (e) {}
        }
      } catch (e) {
        setError(e);
        setLoading(false);
      }
    }
  }, []);

  sendRequest();

  return {
    error,
    sendRequest,
    loading,
    response,
  };
};

export default useLogin;
