import { Dialog } from "@mui/material";
import { useState } from "react";
import {
  Box,
  Button,
  Grid,
  MenuItem,
  TextField,
  Typography,
} from "@mui/material";
import { currencyArray } from "../../../Models/CurrencyModel";
import { useDispatch, useSelector } from "react-redux";
import useFetch from "../../../Hooks/useFetch";
import appConfig from "../../../Utils/Config";
import {
  updateTrip,
  updateTripInStore,
} from "../../../Redux-TS/Reducers/tripListSlice";

const UpdateTrip = (props) => {
  const trip = useSelector((state) => state.activeTrip.trip);
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();

  const [name, setName] = useState(trip.name);
  const [destination, setDestination] = useState(trip.destination);
  const [budget, setBudget] = useState(trip.budget);
  const [currency, setCurrency] = useState(trip.currency);
  const [startDate, setStartDate] = useState(trip.startDate);
  const [endDate, setEndDate] = useState(trip.endDate);
  const { error, loading, sendRequest: sendUpdate } = useFetch();

  const submitHandler = (e) => {
    e.preventDefault();

    const updatedTrip = {
      id: trip.id,
      name,
      destination,
      budget,
      currency,
      startDate,
      endDate,
      isOverBudget: trip.isOverBudget,
      totalSpent: trip.totalSpent,
      users: trip.users,
    };

    sendUpdate(
      {
        url: appConfig.baseUrl + "trip",
        headers: {
          "Content-Type": "application/json",
          token,
        },
        method: "PUT",
        body: JSON.stringify(updatedTrip),
      },
      (t) => {
        dispatch(updateTripInStore(t));
      }
    );
  };

  return (
    <Dialog open={props.open} onClose={props.onClose}>
      <Box component="form" onSubmit={submitHandler} sx={{ m: 4 }}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField
              label="Trip Name"
              variant="standard"
              fullWidth
              onChange={(e) => {
                setName(e.target.value);
              }}
              value={name}
            ></TextField>
          </Grid>
          <Grid item xs={12}>
            <TextField
              label="Destination"
              variant="standard"
              fullWidth
              value={destination}
              onChange={(e) => {
                setDestination(e.target.value);
              }}
            ></TextField>
          </Grid>
          <Grid item xs={6}>
            <TextField
              fullWidth
              select
              label="Currency"
              variant="standard"
              onChange={(e) => {
                setCurrency(e.target.value);
              }}
              value={currency}
            >
              {currencyArray.map((option) => (
                <MenuItem key={option.id} value={option.id}>
                  {option.name} ({option.code})
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Budget"
              variant="standard"
              type="number"
              fullWidth
              value={budget}
              onChange={(e) => {
                setBudget(e.target.value);
              }}
            ></TextField>
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="Start Date"
              variant="standard"
              type="date"
              fullWidth
              InputLabelProps={{
                shrink: true,
              }}
              value={startDate}
              onChange={(e) => {
                setStartDate(e.target.value);
              }}
            ></TextField>
          </Grid>
          <Grid item xs={6}>
            <TextField
              label="End Date"
              variant="standard"
              type="date"
              InputLabelProps={{
                shrink: true,
              }}
              fullWidth
              value={endDate}
              onChange={(e) => {
                setEndDate(e.target.value);
              }}
            ></TextField>
          </Grid>
          <Grid item xs={12} sx={{ textAlign: "center" }}>
            <Typography color="error" size="large">
              {error ? <strong>{error.message}</strong> : ""}
            </Typography>
          </Grid>
        </Grid>

        <Button
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          disabled={loading}
          sx={{ mt: 2 }}
        >
          {loading ? "Updating..." : "Update Trip"}
        </Button>
      </Box>
    </Dialog>
  );
};

export default UpdateTrip;
