import { Button, IconButton, Stack, Typography } from "@mui/material";
import { useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import useFetch from "../../../../Hooks/useFetch";
import appConfig from "../../../../Utils/Config";
import { useDispatch, useSelector } from "react-redux";
import AlertDialog from "../../../AlertDialog";
import { removeExpense } from "../../../../Redux-TS/Reducers/expenseListSlice";

const DeleteExpense = (props) => {
  const [open, setOpen] = useState(false);
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();
  const { error, response, sendRequest: sendDelete } = useFetch();

  const handleClose = () => {
    setOpen(false);
    //rerender the expense list only if an http request was sent
    if (response) {
      dispatch(removeExpense(props.expense.id));
    }
  };

  const handleDelete = () => {
    sendDelete({
      url: appConfig.baseUrl + "expense",
      headers: {
        token: token,
        "Content-Type": "application/json",
      },
      method: "DELETE",
      params: {
        id: props.expense.id,
      },
    });
  };

  const successObj = {
    open,
    onClose: handleClose,
    severity: "success",
    title: "expense deleted successfully",
  };
  const errorObj = {
    open,
    onClose: handleClose,
    severity: "error",
    title: "Error",
    content: error.message,
  };

  const warningObj = {
    open,
    onClose: handleClose,
    severity: "warning",
    title: "Warning",
    content: (
      <>
        <Typography>
          Deleting an expense is a permanent action. Are you sure you want to do
          it?
        </Typography>
        <Stack direction="row" spacing={2} sx={{ my: 1 }}>
          <Button
            color="inherit"
            size="small"
            variant="outlined"
            onClick={handleDelete}
          >
            Yes
          </Button>
          <Button
            color="inherit"
            size="small"
            variant="outlined"
            onClick={() => {
              setOpen(false);
            }}
          >
            No
          </Button>
        </Stack>
      </>
    ),
  };

  return (
    <div>
      <AlertDialog
        {...((response && { ...successObj }) ||
          (error && { ...errorObj }) || { ...warningObj })}
      />

      <IconButton
        onClick={() => {
          setOpen(true);
        }}
      >
        <DeleteIcon />
      </IconButton>
    </div>
  );
};

export default DeleteExpense;
