import { AppBar, Toolbar, Typography } from "@mui/material";
import MenuDrawer from "./MenuDrawer";

const MyAppBar = (props: any) => {
  return (
    <AppBar position="static" sx={{ mb: 2 }}>
      <Toolbar>
        <MenuDrawer />
        <Typography variant="h6" sx={{ mx: 1 }}>
          {props.text}
        </Typography>
      </Toolbar>
    </AppBar>
  );
};

export default MyAppBar;
