import {
  Card,
  CardContent,
  CardHeader,
  FormControlLabel,
  Grid,
  InputAdornment,
  MenuItem,
  TextField,
} from "@mui/material";
import Checkbox from "@mui/material/Checkbox";

import { useState } from "react";
import MyAppBar from "../Components/Layout/MyAppBar";
import useCurrency from "../Hooks/useCurrency";
import { currencyArray } from "../Models/CurrencyModel";

const RateCalculator = () => {
  const [baseCurrency, setBaseCurrency] = useState(1);
  const [targetCurrency, setTargetCurrency] = useState(1);
  const [amount, setAmount] = useState<number>(1);
  const [commission, setCommission] = useState(0);
  const [checked, setChecked] = useState(false);
  const getExchangeRate = useCurrency;
  const textColor = checked ? "primary.main" : "";

  return (
    <>
      <MyAppBar text={"Exchange Rate Calculator"} />
      <Card sx={{ p: 1, m: 1, maxWidth: 400 }}>
        <CardHeader
          // title="Exchange Rate Calculator"
          subheader={"last updated: " + currencyArray[0].updated}
          sx={{ color: "primary.main" }}
        />
        <CardContent>
          <Grid container spacing={2}>
            <Grid item xs={8}>
              <TextField
                fullWidth
                select
                label="From"
                variant="outlined"
                value={baseCurrency}
                onChange={(e: any) => {
                  setBaseCurrency(e.target.value);
                }}
              >
                {currencyArray.map((option) => (
                  <MenuItem key={option.id} value={option.id}>
                    {option.name} ({option.code})
                  </MenuItem>
                ))}
              </TextField>
            </Grid>
            <Grid item xs={4}>
              <TextField
                label="Amount"
                variant="outlined"
                type="number"
                fullWidth
                value={amount}
                onChange={(e: any) => {
                  setAmount(e.target.value);
                }}
              ></TextField>
            </Grid>
            <Grid item xs={8}>
              <TextField
                fullWidth
                select
                label="To"
                variant="outlined"
                value={targetCurrency}
                onChange={(e: any) => {
                  setTargetCurrency(e.target.value);
                }}
              >
                {currencyArray.map((option) => (
                  <MenuItem key={option.id} value={option.id}>
                    {option.name} ({option.code})
                  </MenuItem>
                ))}
              </TextField>
            </Grid>
            <Grid item xs={4}>
              <TextField
                variant="outlined"
                type="number"
                fullWidth
                value={getExchangeRate(
                  baseCurrency,
                  targetCurrency,
                  amount,
                  checked ? commission : 0
                ).toFixed(2)}
                InputProps={{
                  readOnly: true,
                }}
              ></TextField>
            </Grid>
            <Grid item xs={9}>
              <FormControlLabel
                label="add commission rate"
                sx={{ color: textColor }}
                control={
                  <Checkbox
                    checked={checked}
                    onChange={(e: any) => {
                      setChecked(!checked);
                    }}
                  />
                }
              ></FormControlLabel>
            </Grid>

            <Grid item xs={2}>
              {checked && (
                <TextField
                  variant="standard"
                  fullWidth
                  value={commission}
                  onChange={(e: any) => {
                    setCommission(e.target.value);
                  }}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">%</InputAdornment>
                    ),
                  }}
                ></TextField>
              )}
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </>
  );
};

export default RateCalculator;
