import {
  AppBar,
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Container,
  FormControl,
  MenuItem,
  TextField,
  Toolbar,
} from "@mui/material";
import { useEffect, useState } from "react";
import useFileDownload from "../../Hooks/useFileDownload";
import { TripModel } from "../../Models/TripModel";
import useTripService from "../../Service-Hooks/useTripService";
import appConfig from "../../Utils/Config";
import MenuDrawer from "../Layout/MenuDrawer";

const ExcelDownload = () => {
  const [trips, setTrips] = useState<TripModel[]>([]);
  const [selectedTripId, setSelectedTripId] = useState<number>(0);
  const tripService = useTripService();
  const { download, loading } = useFileDownload();
  const downloadUrl = appConfig.tripUrl + "/excel/download/";

  const onDownload = () => {
    download(downloadUrl + selectedTripId);
  };

  useEffect(() => {
    (async () => {
      tripService.getAllTrips().then(
        (arr) => {
          setTrips(arr);
        }
        // (err) => setAlert(getErrorMessage(err), "error")
      );
    })();
  }, []);

  return (
    <>
      <AppBar>
        <Toolbar>
          <MenuDrawer />
        </Toolbar>
      </AppBar>
      <Container sx={{ mt: 10 }}>
        <Card sx={{ maxWidth: "500px" }}>
          <CardHeader title={"Export Trip Expenses to Excel"} />
          <CardContent>
            <FormControl fullWidth>
              <TextField
                label="Trip"
                select
                fullWidth
                value={selectedTripId}
                onChange={(e: any) => {
                  setSelectedTripId(e.target.value);
                }}
                sx={{ my: 1 }}
              >
                {trips.map((trip) => (
                  <MenuItem key={trip.id} value={trip.id}>
                    {trip.name}
                  </MenuItem>
                ))}
              </TextField>
            </FormControl>
          </CardContent>
          <CardActions sx={{ mx: 2, mb: 2 }}>
            <Button variant="contained" onClick={onDownload} disabled={loading}>
              Download Excel File
            </Button>
          </CardActions>
        </Card>
      </Container>
    </>
  );
};

export default ExcelDownload;
