import {
  Card,
  CardContent,
  CardHeader,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Stack,
  Typography,
} from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import { useSelector } from "react-redux";
import { useState } from "react";
import UpdateTrip from "../Components/UserView/TripMethods/UpdateTrip";
import DeleteTrip from "../Components/UserView/TripMethods/DeleteTrip";
import MyAppBar from "../Components/Layout/MyAppBar";
import { useParams } from "react-router-dom";
import AddUserForm from "../Components/TripView/AddUserForm";

const Trip = () => {
  // const trip = useSelector((state) => state.activeTrip.trip);
  const tripList = useSelector((state) => state.tripList.tripList);
  const [menuOpen, setMenuOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [updateOpen, setUpdateOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);
  const [addUserOpen, setAddUserOpen] = useState(false);

  const { tripId } = useParams();
  const trip = tripList.find((t) => t.id === parseInt(tripId));

  const handleClick = (e) => {
    setMenuOpen(true);
    setAnchorEl(e.target);
  };

  return (
    <div>
      <MyAppBar text={trip.name} />
      <Container>
        <Card>
          <CardHeader
            title={trip.name}
            action={
              <IconButton onClick={handleClick}>
                <MoreVertIcon />
              </IconButton>
            }
          />
          <CardContent>
            <Stack spacing={1}>
              <Typography>
                <strong>Destination: </strong> {trip.destination}
              </Typography>
              <Typography>
                <strong>Currency: </strong> {trip.currency}
              </Typography>
              <Typography>
                <strong>Budget: </strong> {trip.budget}
              </Typography>
              <Typography>
                <strong>Total Spent: </strong> {trip.totalSpent}
              </Typography>

              <Typography>
                <strong>Users: </strong>
                {trip.users.map((user) => user.email + " | ")}
              </Typography>

              <Stack spacing={1} mt={1}></Stack>
            </Stack>
          </CardContent>
        </Card>
      </Container>
      <Menu
        anchorEl={anchorEl}
        open={menuOpen}
        onClose={() => {
          setMenuOpen(false);
        }}
      >
        <MenuItem
          onClick={() => {
            setUpdateOpen(true);
            setMenuOpen(false);
          }}
        >
          Edit Trip
        </MenuItem>
        <MenuItem
          onClick={() => {
            setDeleteOpen(true);
            setMenuOpen(false);
          }}
        >
          Delete Trip
        </MenuItem>
        <MenuItem
          onClick={() => {
            setAddUserOpen(true);
            setMenuOpen(false);
          }}
        >
          Add a User
        </MenuItem>
      </Menu>

      <UpdateTrip
        open={updateOpen}
        onClose={() => {
          setUpdateOpen(false);
        }}
      />
      <DeleteTrip
        open={deleteOpen}
        onClose={() => {
          setDeleteOpen(false);
        }}
        severity={"warning"}
        title={"Warning"}
      />
      <AddUserForm
        open={addUserOpen}
        onClose={() => {
          setAddUserOpen(false);
        }}
      />
    </div>
  );
};

export default Trip;
