import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { TripModel } from "../../Models/TripModel";

export interface activeTripState {
  trip: TripModel;
}

const initialState = {
  trip: {},
};

export const activeTripSlice = createSlice({
  name: "active trip",
  initialState,
  reducers: {
    setActive: (state, action: PayloadAction<TripModel>) => {
      state.trip = action.payload;
    },
    emptyActive: (state) => {
      state.trip = {};
    },
  },
});

export const { setActive, emptyActive } = activeTripSlice.actions;

export const activeTrip = (state: any) => state.activeTrip.trip;

export default activeTripSlice.reducer;
