import { currencyArray } from "../Models/CurrencyModel";

//accept base currency and target currency and return the exchange rate

const useCurrency = (
  baseCurrencyId: number,
  targetCurrencyId: number,
  amount: number,
  commission: number
) => {
  let base = currencyArray[baseCurrencyId - 1].rate;
  let target = currencyArray[targetCurrencyId - 1].rate;

  commission /= 100;
  target = target * amount;

  if (base === 1) {
    return target;
  }
  if (target === 1) {
    return target / base;
  }
  if (base !== 1 && target !== 1) {
    const baseUpdate = 1 / base;
    const targetUpdate = 1 / target;

    return (baseUpdate / targetUpdate) * (1 - (commission ? commission : 0));
  } else {
    return 0;
  }
};
export default useCurrency;
