import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardHeader,
  Collapse,
  Stack,
  Typography,
} from "@mui/material";

import Avatar from "@mui/material/Avatar";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUpOutlined";
import { useState } from "react";
import { useSelector } from "react-redux";
import { expenseCategories as catgeories } from "../../../../Models/ExpenseCategories";
import UpdateExpense from "../ExpenseMethods/UpdateExpense";
import DeleteExpense from "../ExpenseMethods/DeleteExpense";
import { currencyArray } from "../../../../Models/CurrencyModel";

const ExpenseCard = (props) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const activeTrip = useSelector((state) => state.activeTrip.trip);
  const currency = activeTrip.currency;
  const label = currencyArray.find((c) => {
    return c.code === currency;
  }).symbol;

  return (
    <Card sx={{ maxWidth: "sm", my: 1 }}>
      <CardActionArea
        onClick={() => {
          setIsExpanded(!isExpanded);
        }}
      >
        <CardHeader
          avatar={
            <Avatar
              sx={{
                bgcolor: catgeories[props.data.category.toLowerCase()].color,
                color: "white",
              }}
            >
              {catgeories[props.data.category.toLowerCase()].icon}
            </Avatar>
          }
          title={props.data.title}
          subheader={props.data.category.toLowerCase()}
          action={
            <Typography
              variant="h6"
              sx={{
                color: "white",
                bgcolor: "secondary.main",
                px: 2,
                py: 0.5,
                borderRadius: "30px",
                m: 1,
              }}
            >
              {parseFloat(props.data.amount).toFixed(2) + " " + label}
            </Typography>
          }
        />

        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="flex-end"
          sx={{ mx: 2, my: 1 }}
        >
          <Typography>{props.data.date}</Typography>

          {isExpanded ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
        </Stack>
      </CardActionArea>

      <Collapse in={isExpanded}>
        <Stack
          direction="row"
          alignItems="flex-start"
          justifyContent="space-between"
        >
          <CardContent>
            <Typography variant="subtitle1">{props.data.note}</Typography>
            <Typography variant="subtitle2">
              Amount: {props.data.localAmount}
            </Typography>
            <Typography variant="subtitle2">
              Currency: {props.data.localCurrencyCode}
            </Typography>
            {/* <Typography variant="subtitle2">User: {props.data.user}</Typography> */}
          </CardContent>
          <CardActions>
            <UpdateExpense expense={props.data} />
            <DeleteExpense expense={props.data} />
          </CardActions>
        </Stack>
      </Collapse>
    </Card>
  );
};

export default ExpenseCard;
