import {
  AppBar,
  Card,
  CardContent,
  CardHeader,
  Container,
  Divider,
  IconButton,
  Stack,
  Toolbar,
  Typography,
} from "@mui/material";
import MenuDrawer from "../Components/Layout/MenuDrawer";
import EditIcon from "@mui/icons-material/Edit";
import { useSelector } from "react-redux";
import jwtDecode from "jwt-decode";

const Profile = () => {
  const authState = useSelector((state) => state.auth);
  const token = localStorage.getItem("token");
  const decodedToken = token ? jwtDecode(token) : {};
  const tripList = useSelector((state) => state.tripList.tripList);

  const email = authState.token ? authState.email : decodedToken.sub;
  const firstName = authState.token
    ? authState.firstName
    : decodedToken.firstName;
  const lastName = authState.token ? authState.lastName : decodedToken.lastName;
  const fullName = firstName + " " + lastName;

  return (
    <>
      <AppBar>
        <Toolbar>
          <MenuDrawer />
          <Typography variant="h6" sx={{ mx: 1 }}>
            {fullName}
          </Typography>
        </Toolbar>
      </AppBar>
      <Container sx={{ mt: 10 }}>
        <Card>
          <CardHeader
            title={fullName}
            action={
              <IconButton>
                <EditIcon />
              </IconButton>
            }
          />
          <CardContent>
            <Stack spacing={1}>
              <Typography>
                <strong>Email: </strong> {email}
              </Typography>

              <Typography>
                <strong>Trips: </strong>
              </Typography>
              <Stack spacing={1} mt={1}>
                {tripList.map((trip) => (
                  <>
                    <Divider />
                    <Typography variant="body2" key={trip.id}>
                      {trip.name}
                    </Typography>
                  </>
                ))}
              </Stack>
            </Stack>
          </CardContent>
        </Card>
      </Container>
    </>
  );
};

export default Profile;
