import { useDispatch, useSelector } from "react-redux";
import { ExpenseModel } from "../Models/ExpenseModel";
import { updateTripInStore } from "../Redux-TS/Reducers/tripListSlice";

const useUpdateTrip = () => {
  const dispatch = useDispatch();
  const activeTrip = useSelector((state: any) => state.activeTrip.trip);
  const expenses = useSelector((state: any) => state.expenseList.expenseList);

  const updateTripTotalSpent = (expense: ExpenseModel) => {
    console.log("using update");

    if (expenses.find((e: ExpenseModel) => e.id === expense.id)) {
      console.log("expense found");
      //create a new trip object with the new total spent amount
      let spent = 0;
      expenses.forEach((e: any) => {
        spent += e.amount;
      });
      const newTrip = { ...activeTrip };
      newTrip.totalSpent = spent;
      //replace the trip in the storage with the new trip
      dispatch(updateTripInStore(newTrip));
    } else {
      console.log("expense not found");
      //create a new trip object with the new total spent amount
      let spent = 0;
      expenses.forEach((e: any) => {
        spent += e.amount;
      });
      spent = expense.amount ? (spent += expense.amount) : spent;
      const newTrip = { ...activeTrip };
      newTrip.totalSpent = spent;
      //replace the trip in the storage with the new trip
      dispatch(updateTripInStore(newTrip));
    }
  };
  return updateTripTotalSpent;
};

export default useUpdateTrip;
