import {
  Alert,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
} from "@mui/material";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import useFetch from "../../Hooks/useFetch";
import { TripModel } from "../../Models/TripModel";
import { updateTripInStore } from "../../Redux-TS/Reducers/tripListSlice";
import appConfig from "../../Utils/Config";

const AddUserForm = (props: IAddUserProps) => {
  const [email, setEmail] = useState("");
  const dispatch = useDispatch();
  const token = useSelector((state: any) => state.auth.token);
  const trip = useSelector((state: any) => state.activeTrip.trip);

  const { error, response, sendRequest: sendEmail } = useFetch();

  const handleClose = () => {
    props.onClose();
  };

  const handleSubmit = () => {
    sendEmail(
      {
        url: appConfig.baseUrl + "trip/add-user",
        body: email,
        method: "POST",
        headers: { token },
        params: {
          tripId: trip.id,
        },
      },
      (t: TripModel) => {
        dispatch(updateTripInStore(t));
      }
    );
  };

  return (
    <>
      <Dialog open={props.open} onClose={handleClose}>
        <DialogTitle>Add user to trip</DialogTitle>

        <Alert severity="warning" sx={{ mx: 3 }}>
          Adding a user to this trip will alow them full access to the expenses
          and details of this trip.
        </Alert>

        <DialogContent>
          <DialogContentText>
            Add the email address of the user you choose to add to this trip.
          </DialogContentText>
          <TextField
            value={email}
            onChange={(e) => {
              setEmail(e.target.value);
            }}
            variant="standard"
            label="email"
            fullWidth
            helperText="only users who have signed up can be added"
          ></TextField>
        </DialogContent>

        {error && (
          <Alert severity="error" sx={{ mb: 2, mx: 3 }}>
            {error.toString()}
          </Alert>
        )}
        {response && (
          <Alert severity="success" sx={{ mb: 2, mx: 3 }}>
            {email + " added to " + trip.name + " successfully"}
          </Alert>
        )}
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleSubmit}>Add User</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default AddUserForm;

export interface IAddUserProps {
  onClose: any;
  open: boolean;
}
