import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { TripModel } from "../Models/TripModel";
import {
  addTripToStore,
  removeTrip,
  setTripList,
  updateTripInStore,
} from "../Redux-TS/Reducers/tripListSlice";
import { RootState } from "../Redux-TS/store";
import appConfig from "../Utils/Config";

const useTripService = () => {
  const dispatch = useDispatch();
  const state = useSelector((state: RootState) => state);
  const tripList = state.tripList.tripList;

  const getAllTrips = async () => {
    if (tripList.length === 0) {
      // fetch the trips:
      const response = await axios.get<TripModel[]>(appConfig.tripUrl); // waiting
      const trips = response.data;

      //this is checked to avoid infinite calls to the server when the array is empty
      if (trips.length > 0) {
        dispatch(setTripList(trips));
        return trips;
      }

      return tripList;
    }
    return tripList;
  };
  const getOneTrip = async (id: number) => {
    // const response = await axios.get<TripModel>(appConfig.tripsUrl + id);
    // const trip = response.data;
    // return trip;
    const trip = tripList.find((p) => p.id === id);
    return trip;
  };

  const addTrip = async (trip: TripModel) => {
    // Send trip to server
    const response = await axios.post<TripModel>(appConfig.tripUrl, trip);
    const addedTrip = response.data;
    // update redux global state
    dispatch(addTripToStore(addedTrip));
  };

  const updateTrip = async (trip: TripModel) => {
    // Senf trip to server for update
    const response = await axios.put<TripModel>(appConfig.tripUrl, trip);
    const updatedTrip = response.data;
    // Update redux global state
    dispatch(updateTripInStore(updatedTrip));
  };

  const deleteTrip = async (id: number) => {
    // Delete in backend
    await axios.delete(appConfig.tripUrl, {
      params: {
        tripId: id,
      },
    });
    dispatch(removeTrip(id));
  };

  return {
    getAllTrips,
    getOneTrip,
    addTrip,
    updateTrip,
    deleteTrip,
  };
};

export default useTripService;
