import {
  AppBar,
  Card,
  LinearProgress,
  Stack,
  Toolbar,
  Typography,
} from "@mui/material";
import MenuDrawer from "../Layout/MenuDrawer";

const TripAppBar = (props) => {
  const remaining = props.trip.budget - props.spent;
  const color = props.trip.overBudget ? "error" : "primary";

  return (
    <AppBar position="static">
      <Toolbar>
        <MenuDrawer />
        <Typography variant="h6" sx={{ flexGrow: 1 }}>
          {props.trip.name}
        </Typography>
      </Toolbar>

      <Card
        sx={{
          mx: 2,
          pt: 2,
          pb: 1,
          borderWidth: 1,
        }}
      >
        <Typography component="div" sx={{ mx: 2 }}>
          <strong> Budget:</strong> {parseFloat(props.trip.budget).toFixed(2)}
        </Typography>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="flex-end"
          mx={2}
        >
          <Typography component="div">
            <strong> Remaining: </strong>
            {parseFloat(remaining).toFixed(2)}
          </Typography>
          <Typography component="div">
            <strong> Spent:</strong>
            {parseFloat(props.spent).toFixed(2)}
          </Typography>
        </Stack>
        <LinearProgress
          variant="determinate"
          value={(remaining / props.trip.budget) * 100}
          sx={{
            mx: 2,
            my: 1,
            p: 0.75,
            borderRadius: "15px",
          }}
          color={color}
        />
      </Card>
      <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="flex-end"
        mx={2}
        my={1}
      >
        <Typography variant="body2"> {props.trip.destination}</Typography>
        <Typography variant="body2">
          {props.trip.startDate}-{props.trip.endDate}
        </Typography>
      </Stack>
    </AppBar>
  );
};

export default TripAppBar;
