import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { TripModel } from "../../Models/TripModel";

export interface TripListState {
  tripList: TripModel[];
}

const initialState: TripListState = {
  tripList: [],
};

export const TripListSlice = createSlice({
  name: "trips",
  initialState,
  reducers: {
    setTripList: (state, action: PayloadAction<TripModel[]>) => {
      state.tripList = action.payload;
    },
    addTripToStore: (state, action: PayloadAction<TripModel>) => {
      state.tripList.push(action.payload);
    },
    updateTripInStore: (state, action: PayloadAction<TripModel>) => {
      console.log(action);
      const indexToUpdate = state.tripList.findIndex(
        (t) => t.id === action.payload.id
      );
      if (indexToUpdate >= 0) state.tripList[indexToUpdate] = action.payload;
    },
    removeTrip: (state, action: PayloadAction<number>) => {
      const indexToDelete = state.tripList.findIndex(
        (t) => t.id === action.payload
      );
      if (indexToDelete >= 0) state.tripList.splice(indexToDelete, 1);
    },
  },
});

export const { setTripList, addTripToStore, updateTripInStore, removeTrip } =
  TripListSlice.actions;

export const getTrips = (state: any) => {
  state.TripListState.tripList();
};

export default TripListSlice.reducer;
