import { configureStore } from "@reduxjs/toolkit";
import authReducer from "./Reducers/authSlice";
import activeTripReducer from "./Reducers/activeTripSlice";
import tripListReducer from "./Reducers/tripListSlice";
import expenseListReducer from "./Reducers/expenseListSlice";

export const store = configureStore({
  reducer: {
    auth: authReducer,
    activeTrip: activeTripReducer,
    tripList: tripListReducer,
    expenseList: expenseListReducer,
  },
});

export type RootState = ReturnType<typeof store.getState>;
