import {
  Box,
  Button,
  Card,
  Container,
  Grid,
  MenuItem,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { useRef, useState } from "react";
import { currencyArray } from "../../../Models/CurrencyModel";
import useFetch from "../../../Hooks/useFetch";
import { useDispatch, useSelector } from "react-redux";
import appConfig from "../../../Utils/Config";
import { addTripToStore } from "../../../Redux-TS/Reducers/tripListSlice";

const AddTripForm = (props) => {
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();

  const [currency, setCurrency] = useState("");
  const nameRef = useRef();
  const destinationRef = useRef();
  const budgetRef = useRef();
  const startDateRef = useRef();
  const endDateRef = useRef();

  const { error, loading, response, sendRequest: sendTrip } = useFetch();

  const submitHandler = (e) => {
    e.preventDefault();
    const trip = {
      name: nameRef.current.value,
      budget: budgetRef.current.value,
      destination: destinationRef.current.value,
      currency,
      startDate: startDateRef.current.value,
      endDate: endDateRef.current.value,
    };

    sendTrip(
      {
        url: appConfig.baseUrl + "trip",
        headers: {
          "Content-Type": "application/json",
          token,
        },
        method: "POST",
        body: JSON.stringify(trip),
      },
      (t) => {
        dispatch(addTripToStore(t));
      }
    );

    nameRef.current.value = "";
    budgetRef.current.value = "";
    destinationRef.current.value = "";
    startDateRef.current.value = "";
    endDateRef.current.value = "";
    setCurrency("");
  };

  return (
    <Container maxWidth="xs">
      <Card sx={{ maxWidth: "xs", p: 3, mt: 10 }}>
        <Stack direction="row" justifyContent="space-between">
          <Typography variant="h6" sx={{ color: "primary.main" }}>
            enter trip details
          </Typography>
          <Button
            size="small"
            variant="outlined"
            color="secondary"
            onClick={props.handleCancel}
          >
            cancel
          </Button>
        </Stack>
        <Box component="form" onSubmit={submitHandler}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                label="Trip Name"
                variant="standard"
                fullWidth
                inputRef={nameRef}
              ></TextField>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Destination"
                variant="standard"
                fullWidth
                inputRef={destinationRef}
              ></TextField>
            </Grid>
            <Grid item xs={6}>
              <TextField
                fullWidth
                select
                label="Currency"
                variant="standard"
                onChange={(e) => {
                  setCurrency(e.target.value);
                }}
                value={currency}
              >
                {currencyArray.map((option) => (
                  <MenuItem key={option.id} value={option.code}>
                    {option.name} ({option.code})
                  </MenuItem>
                ))}
              </TextField>
            </Grid>
            <Grid item xs={6}>
              <TextField
                label="Budget"
                variant="standard"
                type="number"
                fullWidth
                inputRef={budgetRef}
              ></TextField>
            </Grid>
            <Grid item xs={6}>
              <TextField
                label="Start Date"
                variant="standard"
                type="date"
                fullWidth
                InputLabelProps={{
                  shrink: true,
                }}
                inputRef={startDateRef}
              ></TextField>
            </Grid>
            <Grid item xs={6}>
              <TextField
                label="End Date"
                variant="standard"
                type="date"
                InputLabelProps={{
                  shrink: true,
                }}
                fullWidth
                inputRef={endDateRef}
              ></TextField>
            </Grid>
            <Grid item xs={12} sx={{ textAlign: "center" }}>
              <Typography color="error" size="large">
                {error ? <strong>{error.message}</strong> : ""}
              </Typography>

              <Typography color="success.main" size="large">
                {response ? <strong>Trip added successfully!</strong> : ""}
              </Typography>
            </Grid>
          </Grid>

          <Button
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            disabled={loading}
            sx={{ mt: 2 }}
          >
            {loading ? "Adding..." : "Add Trip"}
          </Button>
        </Box>
      </Card>
    </Container>
  );
};

export default AddTripForm;
