import {
  Button,
  Card,
  CardContent,
  CardMedia,
  Container,
  Divider,
  Stack,
  Typography,
} from "@mui/material";
import { useDispatch } from "react-redux";
import { NavLink as RouterLink, useNavigate } from "react-router-dom";
import logo from "../Assets/logo.png";
import useLogin from "../Hooks/useLogin";
import { login } from "../Redux-TS/Reducers/authSlice";
import appConfig from "../Utils/Config";

const Home = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { sendRequest: sendLogin } = useLogin();
  const setAuthData = (response) => {
    dispatch(login(response));
    navigate("/user");
  };
  const demoLogin = (e) => {
    e.preventDefault();

    sendLogin(
      {
        url: appConfig.baseUrl + "login",
        body: JSON.stringify({
          email: "demouser@demo.com",
          password: "demouser",
        }),
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      },
      setAuthData
    );
  };

  return (
    <div>
      <Container>
        <Card
          elevation={3}
          sx={{
            maxWidth: 400,
            my: 2,
            p: 1,
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            color: "primary.main",
          }}
        >
          <CardMedia
            component="img"
            height="400"
            image={logo}
            sx={{ borderRadius: "10px" }}
          />

          <CardContent>
            <Typography>
              <strong> Welcome!</strong> What would you like to do?
            </Typography>
            <Stack
              alignItems="center"
              spacing={2}
              direction="row"
              justifyContent="space-evenly"
              mt={2}
            >
              <Button variant="contained" component={RouterLink} to="/login">
                Login
              </Button>
              <Divider orientation="vertical" flexItem />
              <Button variant="contained" component={RouterLink} to="/signup">
                Sign Up
              </Button>
              <Divider orientation="vertical" flexItem />

              <Button variant="contained" onClick={demoLogin}>
                Demo
              </Button>
            </Stack>
          </CardContent>
        </Card>
      </Container>
    </div>
  );
};

export default Home;
