import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import jwtDecode from "jwt-decode";

export enum clientEnum {
  "USER",
  "ADMIN",
}

export interface TokenValues {
  exp: number;
  iat: number;
  id: number;
  firstName: string;
  lastName: string;
  clientType: clientEnum;
  sub: string;
}

export interface AuthState {
  isLoggedIn: boolean;
  token: string;
  id: number;
  email: string;
  firstName: string;
  lastName: string;
  clientType: clientEnum;
}

const initialState: AuthState = {
  isLoggedIn: false,
  token: "",
  id: 0,
  email: "",
  firstName: "",
  lastName: "",
  clientType: clientEnum.USER,
};

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    login: (state, action: PayloadAction<string>) => {
      //persist token to local storage
      localStorage.setItem("token", action.payload);
      state.isLoggedIn = true;
      state.token = action.payload;

      if (state.token) {
        const decodedToken: TokenValues = jwtDecode(state.token);
        state.email = decodedToken.sub;
        state.id = decodedToken.id;
        state.firstName = decodedToken.firstName;
        state.lastName = decodedToken.lastName;
        state.clientType = decodedToken.clientType;
      }
    },
    logout: (state) => {
      state.isLoggedIn = false;
      localStorage.clear();
      //this method is used by the root reducer to clear the app state when logging out
    },
  },
});

export const { login, logout } = authSlice.actions;

export const isLoggedIn = (state: any) => state.AuthState.isLoggedIn;

export default authSlice.reducer;
