import {
  Card,
  CardActionArea,
  CardContent,
  CardHeader,
  Typography,
} from "@mui/material";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { setActive } from "../../Redux-TS/Reducers/activeTripSlice";

const TripCard = (props) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const dates = props.data.startDate + " - " + props.data.endDate;

  return (
    <Card sx={{ maxWidth: "sm", my: 1 }}>
      <CardActionArea
        onClick={() => {
          dispatch(setActive(props.data));
          navigate("/trip/" + props.data.id);
        }}
      >
        <CardHeader
          title={props.data.name}
          subheader={dates}
          sx={{
            color: "primary.main",
            mb: -2,
          }}
        />

        <CardContent>
          <Typography component="div">
            <strong> Budget:</strong> {parseFloat(props.data.budget).toFixed(2)}
          </Typography>
          {/* <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="flex-end"
          >
            <Typography component="div">
              <strong> Remaining:</strong>{" "}
              {parseFloat(props.data.budget - props.data.totalSpent).toFixed(2)}
            </Typography>
            <Typography component="div">
              <strong> Spent:</strong>{" "}
              {parseFloat(props.data.totalSpent).toFixed(2)}
            </Typography>
          </Stack>
          <LinearProgress
            variant="determinate"
            value={50}
            sx={{
              p: 0.5,
              my: 1,
              borderRadius: "10px",
            }}
          /> */}
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

export default TripCard;
