import { Button, Stack, Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import useFetch from "../../../Hooks/useFetch";
import { emptyActive } from "../../../Redux-TS/Reducers/activeTripSlice";
import { removeTrip } from "../../../Redux-TS/Reducers/tripListSlice";
import appConfig from "../../../Utils/Config";
import AlertDialog from "../../AlertDialog";

const DeleteTrip = (props: any) => {
  const { error, response, sendRequest: sendDelete } = useFetch();
  const token = useSelector((state: any) => state.auth.token);
  const tripId = useSelector((state: any) => state.activeTrip.trip.id);
  const dispatch = useDispatch();

  const navigate = useNavigate();

  const open = props.open;
  const handleClose = () => {
    props.onClose();
    if (response) {
      dispatch(removeTrip(tripId));
      dispatch(emptyActive());
      navigate("/user");
    }
  };

  const handleDelete = () => {
    sendDelete(
      {
        url: appConfig.baseUrl + "trip",
        headers: {
          token: token,
          "Content-Type": "application/json",
        },
        method: "DELETE",
        params: {
          tripId: tripId,
        },
      },
      handleClose
    );
  };

  const successObj = {
    open,
    onClose: handleClose,
    severity: "success",
    title: "trip deleted successfully",
    content: "",
  };
  const errorObj = {
    open,
    onClose: handleClose,
    severity: "error",
    title: "Error",
    content: error.toString(),
  };

  const warningObj = {
    open,
    onClose: handleClose,
    severity: "warning",
    title: "Warning",
    content: (
      <>
        <Typography>
          Deleting a trip is a permanent action. Are you sure you want to do it?
        </Typography>
        <Stack direction="row" spacing={2} sx={{ my: 1 }}>
          <Button
            color="inherit"
            size="small"
            variant="outlined"
            onClick={handleDelete}
          >
            Yes
          </Button>
          <Button
            color="inherit"
            size="small"
            variant="outlined"
            onClick={handleClose}
          >
            No
          </Button>
        </Stack>
      </>
    ),
  };

  return (
    <AlertDialog
      {...((response && { ...successObj }) ||
        (error && { ...errorObj }) || { ...warningObj })}
    ></AlertDialog>
  );
};

export default DeleteTrip;
